/* ============================================
1.               Start Header CSS
===============================================*/

.header_top {
  margin: 7px 0;
}
.header_top_area {
  background: #252b3f none repeat scroll 0 0;
}
.main_menu .navbar-default{
  background: transparent;
  border: none;
}
.header_bottom_area {
  background: #e9eff2 none repeat scroll 0 0;
}
.header_top_menu {
  margin: 8px 0;
}
.header_top_menu a {
  border-right: 1px solid #fff;
  color: #fff;
  font-family: "Oxygen";
  font-size: 12px;
  padding: 0 14px;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.header_top_menu a:hover{
  color: #017cc0;
}
.header_top_menu ul li:first-child a{
  padding: 0 16px 0 0;
  text-transform: uppercase;
}
.header_top_menu ul li:last-child a{
  border-right: none;
}
.header_top_social i {
  color: #959595;
  display: block;
  height: 40px;
  line-height: 40px;
  text-align: center;
  width: 40px;
  font-size: 15px;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.header_top_social a:hover i{
  color: #fff;
}
.header_top_social a:hover i.fa-twitter{
  background: #55acee;
}
.header_top_social a:hover i.fa-facebook{
  background: #3b5998;
}
.header_top_social a:hover i.fa-google-plus{
  background: #e51c23;
}
.header_top_social a:hover i.fa-dribbble{
  background: #EA4C89;
}
.header_top_social a:hover i.fa-linkedin{
  background: #007BB6;
}


.header_logo {
  margin: 24px 0;

  @media (max-width: 980px) {
    display: flex;

    .header_top_service {
      .endereco, .horario {
        display: none;
      }
      .contato {
        dl {
          width: 100%;
        }
        i {
          display: none;
        }
      }
    }
  }
}
.header_we_are > dl {
  float: left;
  margin-bottom: 0;
  margin-right: 9px;
  text-align: right;
  width: 68%;
}
.header_we_are i {
  line-height: 54px;
  text-align: center;
  width: 27%;
  display: inline-block;
}
.header_we_are dt {
  color: #666666;
  font-family: "RobotoMedium";
  font-size: 13px;
  font-weight: 400;
  text-transform: uppercase;
}
.header_we_are dt, .header_we_are dd{
  line-height: 28px;
}
.header_we_are dd, .header_we_are dd a {
  color: #959595;
}
.header_we_are dd a:hover{
  color: #017cc0;
}


.main_menu .navbar-default .navbar-nav{
  width: 100%;
}
.main_menu .navbar-default .navbar-nav > li > a{
  color: #252b3f;
  font-family: "RobotoLight";
  padding: 24px 38px;
  text-transform: uppercase;
  display: block;
}
.main_menu .nav.navbar-nav li:hover > a {
  background: #f1f5f7;
}
.main_menu .nav.navbar-nav li.dropdown > a {
  background: url("images/dropdown_bg.png") no-repeat scroll 90% center;
}
.main_menu .nav.navbar-nav li.dropdown:hover > a {
  background: #f1f5f7 url("images/dropdown_bg.png") no-repeat scroll 90% center;
}
.main_menu .navbar-nav > li > .dropdown-menu{
  top: 99%;
  min-width: 200px;
  background-color: #f1f5f7;
  text-transform: uppercase;
}
.main_menu .navbar-nav > li > .dropdown-menu li:hover a{
  background-color: #e9eff2;
}
.main_menu .dropdown-menu > li > a{
  border-bottom: 1px solid #e4e4e4;
  font-family: "RobotoLight";
  padding: 10px 25px;
}
.main_menu .dropdown-menu > li:last-child > a{
  border-bottom: none;
}
.main_menu .navbar-default .navbar-nav > li:last-child{
  float: right;
}
.main_menu .navbar-default .navbar-nav > li:last-child > a{
  background: #1874c1;
  color: #fff;
  font-family: 'RobotoMedium';
}
.main_menu .navbar-default .navbar-nav > li.active:last-child > a{
  background-color: #0060b1;
  color: #fff;
}
.main_menu .navbar-default .navbar-nav > li:last-child > a:hover{
  background: #0060b1;
}
.main_menu .navbar-default .navbar-nav > li > a:hover{
  background: #f1f5f7;
}
.main_menu .navbar {
  margin: 0;
}
.main_menu .navbar-default .navbar-nav > li.active > a{
  color: #252b3f;
  background: #f1f5f7;
}


/* ============================================
2.              Start Main Slider CSS
===============================================*/

.main_slider_area {
  position: relative;
}

/* ============================================
3.              Start Why Us CSS
===============================================*/
.single-card {
  position: relative;
  z-index: 1;
  background: #fff;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  padding: 30px;
  overflow: hidden;
}
.single_why_hover {
  background: rgb(48, 141, 199) none repeat scroll 0 0;
  height: 100%;
  position: absolute;
  width: 100%;
  z-index: -1;
  opacity: 0;
  left: 0;
  top: 0;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  -webkit-transition: 0.5s;
  transition: 0.5s;

}
.single-card:hover .single_why_hover{
  opacity: 0.75;
  animation-name: fadeInUp;
  backface-visibility: visible !important;
  animation-duration: 1s;
  animation-fill-mode: both;
}
.single-card h5 {
  margin-top: 22px;
  margin-bottom: 21px;
}
.single-card:hover h5, .single-card:hover  p{
  color: #fff;
}
.single-card .icon_one{
  display: inline-block;
  width: 74px;
  height: 114px;
  background: url(images/logistic-service.png) no-repeat center center;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.single-card:hover .icon_one{
  background: url(images/logistic-service-hover.png) no-repeat center center; ;
}
.single-card .icon_two{
  display: inline-block;
  width: 74px;
  height: 114px;
  background: url(images/warehousing.png) no-repeat center center; 
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.single-card:hover .icon_two{
  background: url(images/warehousing-hover.png) no-repeat center center;
}
.single-card .icon_three{
  display: inline-block;
  width: 74px;
  height: 114px;
  background: url(images/customer-service.png) no-repeat center center; 
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.single-card:hover .icon_three{
  background: url(images/customer-service-hover.png) no-repeat center center; ;
}

/* ============================================
4.            start we are best
===============================================*/

.weare_best_left_service .single_weare_best > dl {
