/* ============================================
5.              Start Get Quote Area
===============================================*/

.get_quote_area {
  background: rgba(0, 0, 0, 0) url("images/get-a-quote.jpg") no-repeat scroll 67% center / cover ;
  color: #fff;
  padding: 90px 0;
}
.get_quote_area .section_title > h4 {
