    }
  }
}


/* ============================================
6.              Start Counter Area
===============================================*/

.logistic_counter_area{
  background: rgba(0, 0, 0, 0) url("images/counter-bg.jpg") no-repeat scroll center center / cover ;
}
.single-counter{
  text-align: center;
  padding: 90px 0;

  img {
    display: inline-block;
  }

  & > h4 {
  color: #222222;
  font-size: 14px;
  font-family: 'RobotoMedium';
}

