9.          Start From Our Blog Area
===============================================*/

.single_blog_comment ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.single_blog_comment li a {
  border: 1px solid #4690cd;
  border-radius: 5px;
  display: inline-block;
  line-height: 35px;
